.container {
    grid-area: Register;
    border: 1px solid black;
    width: 50%;
    min-width: 250px;
    height: 25rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 8px;
}

.title{

    color: rgb(54, 54, 54);
    font-size: 2rem;
}

.form{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.label{
    margin: 5px;
    width: 90%;
    text-align: left;
    font-size: 1.2rem;
    font-weight: bold;
    color: rgb(47, 93, 136);
}

.input{
    width: 90%;
}
.submit{
    margin: 10px;
    border-radius: 10px;
    border: 1px solid black;
    background: none;
    height: 22px;
}

